.public-user-profile {
  padding-top: 10px;
}
.public-user-profile .user-name {
  margin-bottom: 10px;
  margin-top: 10px;
  line-break: anywhere;
}
.public-user-profile .user-name > * {
  margin-right: 5px;
}
.public-user-profile .details-container {
  padding: 0 20px 0 0;
}
.public-user-profile .details-container h3 {
  margin: 5px 0 10px 0;
}
.public-user-profile .details-container > div {
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.public-user-profile .badge-container {
  font-size: 25px;
}
.public-user-profile .badge-container > div {
  margin-bottom: 10px;
  display: flex;
  flex-direction: column;
}
.public-user-profile .badge-container > div .q-badge {
  width: fit-content;
  margin: 5px;
  font-size: 18px;
  height: 23px;
  cursor: pointer !important;
}
.public-user-profile .roundtrip-container {
  margin-bottom: 10px;
}
